footer {
  background-color: #1e1e1e;
  padding: 0px 32px 39px 31px;
}
.footer-link {
  display: flex;
}
.flex-items a {
  transition: width 2s;
  color: #ccc;
}
.flex-items a:hover {
  color: white;
  padding-bottom: 4px;
  border-bottom: 2px solid white;
}
nav {
  flex: 1 1 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 0 -10px;
}
.flex-items {
  flex: 0 1 auto;
  padding: 0 10px 10px;
  color: white;
}
.footer-link-holder {
  display: flex;
  flex-wrap: wrap;
  width: 30%;
  flex-direction: column;
  height: 300px;
  justify-content: flex-start;

  font-size: inherit;
  color: white;
}
.logo-holder a {
  text-align: left;
}
.logo {
  margin: 10px 5px 0px 0;
}
.logo-holder .logo:hover {
  animation: zoomIn 1s;
}
.flex-items ul {
  margin: 0;
  padding: 0;
}
.flex-items li {
  list-style: none;
  margin-bottom: 1em;
  padding: 0;
}
.social-logos {
  display: flex;
  margin: 2%;
  padding: 0px;
  width: 23px;
  height: 23px;

  margin-left: 0.6rem;
  margin-right: 0.6rem;
}
.social-logos a:hover {
  cursor: pointer;
  animation: zoomIn 1s;
}
.bottom-bar p {
  color: #ccc;
  margin: 20px;
  font-size: 16px;
}
.imp-logos {
  display: flex;

  gap: 10px;
  margin: 5 0 0 10px;
  padding: 0 0 10px 0;
}
@media screen and (max-width: 768px) {
  footer {
    padding-left: 16px;
  }
  .footer-link {
    flex-direction: column;
  }
  .logo-holder {
    height: fit-content;
  }
  .logo-holder a {
    text-align: left;
  }
  .imp-logos {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin: 5px 18px;
    gap: 10px;
  }
  .logo {
    margin: 5px 0;
  }
  nav {
    justify-content: space-between;
    margin: 0 0 0 25px;
  }
  .flex-items {
    padding: 2px;
  }
  .social-logos {
    margin: 10px;
    padding: 0 5px;
  }
  .bottom-bar {
    padding: 10px;
  }
  a:hover {
    text-decoration: none;
  }
  .ratings {
    width: 320px !important; 
    height: 150px !important;
  }
}
